@import 'variables.scss';


.chakra-table {
    a {
        color: steelblue;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Mtavruli";
}

.bt {
    position: absolute;
    z-index: 10;
    bottom: 10px;
}

.site_container {
    width: 75%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ebedff;
    border-radius: 25px 25px 0 0;
    justify-content: flex-start;
    min-height: 90dvh;
}

.app_wrapper {
    width: 90% !important;
}

@media screen and (min-width: 1600px) {
    .site_container {
        width: 75vw;

        .app_wrapper {
            padding: 50px 0;
        }
    }

    .bottom_footer,
    .upper_footer {
        padding-left: 12vw !important;
        padding-right: 12vw !important
    }

    .nav_wrapper {
        width: 75% !important;
    }
}

@media screen and (min-width: 2000px) {
    .site_container {
        width: 50vw !important;

        .app_wrapper {
            padding: 50px 0;
        }
    }

    .nav_wrapper {
        width: 70% !important;
    }

    .bottom_footer,
    .upper_footer {
        padding-left: 25vw !important;
        padding-right: 25vw !important
    }
}

.app_wrapper {
    padding: 50px 0px;
}

.videos_wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 40px;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
}

@media screen and (max-width: 700px) {
    .app_wrapper {
        padding: 50px 0px;
    }
}

@media screen and (max-width: 1000px) {
    .site_container {
        width: 100vw !important;

        .app_wrapper {
            width: 100% !important;
        }
    }
}

@media screen and (max-width: 1500px) {
    .site_container {
        width: 90vw !important;

        .app_wrapper {
            width: 90% !important;
        }
    }
}